.card-container {
  display: flex;
  flex-direction: column;
  padding: 25px;
  background-color: rgb(71, 71, 187);
  border-radius: 10px;
  color: white;
  cursor: pointer;
  transform: translateZ(0);
  transition: transform 0.25s ease-in-out;
}

.card-container:hover {
  transform: scale(1.05);
}
